  background-color: #102654;
}
@media (max-width: 767px) {
  .event__right-side {
    height: 292px;
  }
}

.event__image {
  display: block;
  width: 660px;
  height: 100%;
  object-fit: cover;
}
@media (max-width: 1199px) {
  .event__image {
    width: 100%;
  }
}

.event__title {
  font-size: 82px;
  font-family: "Alumni Sans", "Arial", sans-serif;
  font-weight: 600;
  line-height: 78px;
  text-transform: uppercase;
  letter-spacing: 0.03em;
}
@media (max-width: 1199px) {
  .event__title {
    font-size: 48px;
    line-height: 50px;
  }
}

.event__description {
  grid-column: 1/3;
  margin-bottom: 23px;
  font-size: 18px;
  line-height: 20px;
}
@media (max-width: 1199px) {
  .event__description {
    margin-bottom: 16px;
    font-size: 12px;
    line-height: 18px;
  }
}

.event__btn {
  grid-column: 1/3;
  padding: 29px;
}
@media (max-width: 1199px) {
  .event__btn {
    padding: 16px;
  }
}

.event__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-direction: column;
  gap: 24px;
}
@media (max-width: 1199px) {
  .event__list {
    gap: 10px;
  }
}

.event__item-title {
  font-size: 18px;
  font-weight: 400;
  line-height: 18px;
  margin-bottom: 18px;
}
@media (max-width: 1199px) {
  .event__item-title {
    margin-bottom: 10px;
    font-size: 12px;
    line-height: 12px;
  }
}

.event__item-caption {
  position: relative;
  display: flex;
  gap: 7px;
  width: max-content;
  font-size: 40px;
  line-height: 40px;
  font-weight: 700;
}
.event__item-caption::before {
  content: "";
  position: absolute;
  top: -9px;
  left: 0;
  right: 0;
  height: 1px;
  background-color: #ffffff;
}
@media (max-width: 1199px) {
  .event__item-caption {
    align-items: center;
    font-size: 24px;
    line-height: 24px;
  }
  .event__item-caption::before {
    top: -5px;
  }
}

.event__item-small {
  font-size: 14px;
  font-weight: 400;
  line-height: 18px;
}
@media (max-width: 1199px) {
  .event__item-small {
    font-size: 10px;
    line-height: 15px;
  }
}

