@use "../variables";
@use "../mixins";

.tabs-content {
  position: relative;
  margin: 0;
  padding: 0;
  list-style: none;
  z-index: 1;

  &::after {
    content: "";
    position: absolute;
    right: -212px;
    bottom: -216px;
    width: 321px;
    height: 384px;
    z-index: -1;
    background-image: url("../img/subscription/background.png");
    background-size: contain;
    background-repeat: no-repeat;
    transform: rotate(150deg);

    @include mixins.supports-webp("../img/subscription/background");

    @include mixins.retina {
      background-image: url("../img/subscription/background@2x.png");

      @include mixins.supports-webp("../img/subscription/background@2x");
    }
  }

  @include mixins.vp-tablet {
    &::after {
      bottom: -192px;
      right: -74px;
    }
  }

  @include mixins.vp-mobile {
    &::after {
      width: 0;
    }
  }
}

.tabs-content__list {
  margin: 0;
  padding: 0;
  display: none;
  list-style: none;

  &--active {
    display: grid;
    gap: 40px;
    grid-template-columns: 1fr 1fr 1fr;

    @include mixins.vp-tablet {
      justify-content: center;
      grid-template-columns: 442px;
    }

    @include mixins.vp-mobile {
      grid-template-columns: 1fr;
      gap: 35px;
    }
  }
}

.tabs-content__item {
  position: relative;
  padding: 68px 20px;
  background-color: variables.$color-default-white;
  border: 7px solid transparent;
  transition: all variables.$trans-default;

  &:hover {
    border-image: linear-gradient(148.82deg,#ED0233 .85%,#003EB7 100%) 1;
  }
}

.tabs-content__title {
  margin-bottom: 21px;
  font-size: 26px;
  font-weight: 700;
  line-height: 30px;

  @include mixins.vp-mobile {
    margin-bottom: 19px;
    font-size: 22px;
    line-height: 24px;
  }
}

.tabs-content__caption {
  margin-bottom: 96px;
  font-weight: 400;

  @include mixins.vp-mobile {
    margin-bottom: 81px;
  }
}

.tabs-content__price {
  position: relative;
  margin-bottom: 59px;
  font-size: 60px;
  font-weight: 700;
  line-height: 60px;
  color: variables.$color-additional;

  &::after {
    content: "";
    display: inline-block;
    margin-left: 14px;
    height: 42px;
    width: 30px;
    background-image: url("../img/svg/ruble.svg");
    background-size: cover;
  }

  @include mixins.vp-mobile {
    margin-bottom: 50px;
    font-size: 48px;

    &::after {
      margin-left: 10px;
      height:33px;
      width: 24px;
    }
  }
}

.tabs-content__shadow-price {
  position: absolute;
  bottom: 6px;
  width: 100%;
  font-weight: 700;
  font-size: 100px;
  line-height: 100px;
  color: rgba(232,232,232,.48);
  overflow-wrap: normal;
  user-select: none;

  @include mixins.vp-mobile {
    font-size: 80px;
    line-height: 80px;
  }
}

.tabs-content__price-wrapper {
  position: relative;
}

.tabs-content__btn {
  margin: 0 auto;
  width: 206px;
  padding: 27px;
}

